html, body {
    background-color: #F7FAFF;
    overflow: hidden;
  }
  
  ::-webkit-scrollbar{
    width:0;
    background-color:transparent;
  }
  
  img, p {
    user-select: none;
    -webkit-user-drag: none;
  }
  
  select, input[type="text"]{
    width:100%;
    box-sizing:border-box;
  }
  
  *:focus {
    outline: none;
  }
  
  .mainContainer {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-flow: row nowrap;
    border: 0;
    padding: 0;
    margin: 0;
    overflow: hidden;
  }
  
